import React, { Component } from 'react';
import { Table } from 'antd';
// import { setTableCourseListForOpenClass } from 'dump/data';

class TableCourseList extends Component {
  constructor(props) {
    super(props);
    this.state = {};

    this.columns = [
      {
        title: 'Ký hiệu',
        dataIndex: 'subjectId',
        key: 'subjectId',
      },
      {
        title: 'Tên học phần',
        dataIndex: 'subjectLabel',
        key: 'subjectLabel',
      },
      {
        title: 'Số tín chỉ',
        dataIndex: 'noOfCredit',
        key: 'noOfCredit',
      },
      {
        title: 'Số lớp hành chính',
        dataIndex: 'noOfClassCreditEnrollment',
        key: 'noOfClassCreditEnrollment',
      },
      {
        title: 'Số SV học lần 1',
        dataIndex: 'noOfStudentRegister',
        key: 'noOfStudentRegister',
      },
      {
        title: 'Số SV chưa học',
        dataIndex: 'noOfStudentNotRegister',
        key: 'noOfStudentNotRegister',
      },
      {
        title: 'Số SV học lại',
        dataIndex: 'noOfStudentReLearn',
        key: 'noOfStudentReLearn',
      },
      {
        title: 'Số SV học ngành 2',
        dataIndex: 'noOfStudentDegreeTwo',
        key: 'noOfStudentDegreeTwo',
      },
      {
        title: 'Tổng số SV',
        dataIndex: 'noOfStudents',
        key: 'noOfStudents',
      },
    ];

    this.rowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
      },
      getCheckboxProps: record => ({
        disabled: record.name === 'Disabled User', // Column configuration not to be checked
        name: record.name,
      }),
    };
  }

  render() {
    const { data, isLoading } = this.props;
    return (
      <Table
        bordered
        rowSelection={this.rowSelection}
        dataSource={data}
        columns={this.columns}
        loading={isLoading}
      />
    );
  }
}

export default TableCourseList;



