import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Col, Divider } from 'antd';
import { CartHeadAction } from 'components/common';
import { isEmpty } from 'lodash';
import { exportClassCredit, exportClassCreditExcel } from 'services/classCredit';
import MenuTabTop from './MenuTabTop';
import FormInfoCommon from './FormInfoCommon';
import TableCourseList from './TableCourseList';
import PopupClassCreadit from './PopupClassCreadit';

import './style.scss';

class StatisticsDataForOpenClassPage extends Component {
  state = {
    tableData: [],
    popupSearchVisible: false,
    searchParams: {},
    isSearching: false,
    isExporting: false
  };

  closePopup = () => {
    this.setState({ popupSearchVisible: false });
  }

  handleSearch = async params => {
    this.setState({ searchParams: params, isSearching: true });
    try {
      const res = await exportClassCredit(params);
      if(res.data) {
        this.setState({ tableData: res.data.items });
      }
    } catch (err) {
      console.log(err);
    } finally {
      this.setState({ isSearching: false });
    }
  }

  handleCreateClassCreadit = () => {
    this.setState({ popupSearchVisible: true });
  }

  handleExportExcel = async() => {
    try {
      this.setState({ isExporting: true });
      await exportClassCreditExcel(this.state.searchParams);
    } catch(err) {
      console.log(err);
    } finally {
      this.setState({ isExporting: false });
    }
  }

  render() {
    const { tableData, popupSearchVisible, isSearching, isExporting } = this.state;
    const isBtnExportDisable = isEmpty(tableData);
    return (
      <Card title="Thống kê số liệu mở lớp tín chỉ">
        <CartHeadAction>
          <MenuTabTop
            handleCreateClassCreadit={this.handleCreateClassCreadit}
            handleExportExcel={this.handleExportExcel}
            isBtnExportDisable={isBtnExportDisable}
            isExporting={isExporting}
          />
        </CartHeadAction>
        <Row>
          <Col span={24}>
            <FormInfoCommon handleSearch={this.handleSearch} />
          </Col>
        </Row>
        <Divider>Danh sách tổng hợp</Divider>
        <Row>
          <Col span={24}>
            <TableCourseList data={tableData} isLoading={isSearching} />
          </Col>
        </Row>
        <PopupClassCreadit visible={popupSearchVisible} closePopup={this.closePopup} />
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <StatisticsDataForOpenClassPage />
  </Page>
);



