import React, { Component } from 'react';
// import { Row, Col, Form, Select, Modal } from 'antd';
import { Form, Modal, Row, Col, Divider, Button, message } from 'antd';
import { isEmpty } from 'lodash';
import { searchSubject, createClassCredit } from 'services/classCredit';
import { BtnAddNew } from 'components/common/button';
import FormInfoCommonCreateNewClass from './FormInfoCommonCreateNewClass';
import TableCreateNewClass from './TableCreateNewClass';
// import { isEmpty } from 'lodash';
// import FilterPathEducate from 'components/common/input/FilterPathEducate';
// import YearSemesterInput from 'components/common/input/YearSemesterInput';
// import { schoolYearsTime } from 'dump/data';
// import { BtnSearch, BtnClear } from 'components/common/button';
import './popupClassCreadit.scss';

// const { Option } = Select;

@Form.create({ name: 'formSerchIndividual' })
class PopupClassCreadit extends Component {
  state = {
    tableData: [],
    selectedRows: [],
    isSaving: false,
    isSearching: false,
    reload: 0
  };

  handleCreate = async() => {
    try {
      const { selectedRows } = this.state;
      const formValues = this.props.form.getFieldsValue()
      const tableData = [...this.state.tableData];
      const updatingItems = [];
      this.setState({ isSaving: true });
      if(!isEmpty(tableData)) {
        tableData.forEach((item, index) => {
          if(selectedRows.includes(index)) {
            item.id = item.classCreditId;
            item.trainTypeId = formValues.trainTypeId;
            item.facultyId = formValues.facultyId;
            item.studyFieldId = formValues.studyFieldId;
            item.registerSessionId = formValues.registerSessionId;
            item.isLocked = false;
            item.fromWeek = formValues.fromWeek;
            item.toWeek = formValues.toWeek;
            item.toWeek = formValues.toWeek;
            item.fromDate = formValues.fromDate;
            item.toDate = formValues.toDate;
            item.periodInWeek = formValues.periodInWeek;
            item.minStudentNo = formValues.minStudentNo;
            item.maxStudentNo = formValues.maxStudentNo;
            item.classCode = formValues.classCode

            updatingItems.push(item);
          }
        })
      }
      if(!isEmpty(updatingItems)) {
        createClassCredit(updatingItems);
        this.setState({ reload: this.state.reload + 1 });
        message.success("Tạo lớp tín chỉ thành công!");
      }
    } catch (err) {
      console.log(err);
    } finally {
      this.setState({ isSaving: false });
    }
  }

  handleSearchSubject = async params => {
    try {
      this.setState({ isSearching: true });
      const res = await searchSubject(params);
      if(res.data) {
        this.setState({ tableData: res.data.items });
      }
    } catch (err) {
      message.error('Có lỗi xãy ra!')
    } finally {
      this.setState({ isSearching: false });
    }
  }

  handleSelectRow = selectedRows => {
    this.setState({ selectedRows });
  }

  handleCancel = () => {
    this.props.closePopup();
  }

  render() {
    const { isSaving, isSearching, reload, tableData, selectedRows } = this.state;
    const { visible } = this.props;
    const isBtnAddDisable = isEmpty(selectedRows);
    return (
      <Modal
        className="popup-class-creadit"
        title="Tạo lớp tín chỉ"
        visible={visible}
        onCancel={this.handleCancel}
        footer={[
          <Button key="back" onClick={this.handleCancel}>
            Đóng
          </Button>,
        ]}
      >
        <Row>
          <Col span={24}>
            <FormInfoCommonCreateNewClass
              handleSearchSubject={this.handleSearchSubject}
              form={this.props.form}
              reload={reload}
            />
          </Col>
        </Row>
        <Divider>Danh sách lớp TC</Divider>
        <BtnAddNew
          title="Tạo lớp tín chỉ"
          onClick={this.handleCreate}
          loading={isSaving}
          disabled={isBtnAddDisable}
        />
        <Row>
          <Col span={24}>
            <TableCreateNewClass
              data={tableData}
              handleSelectRow={this.handleSelectRow}
              loading={isSearching}
            />
          </Col>
        </Row>
      </Modal>
    );
  }
}

export default PopupClassCreadit;



