import React, { Component } from 'react';
import { Form } from 'antd';
import { BtnAddNew, ButtonGroup, BtnExport } from 'components/common/button';
import './style.scss';

@Form.create({ name: 'formMenuTabTop' })
class MenuTabTop extends Component {
  state = {};
  render() {
    const { handleExportExcel, isBtnExportDisable, isExporting } = this.props;
    return (
      <ButtonGroup>
        <BtnAddNew
          title="Tạo lớp tín chỉ"
          onClick={this.props.handleCreateClassCreadit}
        />
        <BtnExport
          title="Xuất Excel"
          onClick={handleExportExcel}
          disabled={isBtnExportDisable}
          loading={isExporting}
        />
      </ButtonGroup>
    );
  }
}

export default MenuTabTop;



