import React, { Component } from 'react';
import { Row, Col, Form, Select } from 'antd';
import { isEmpty } from 'lodash';
import FilterPathEducate from 'components/common/input/FilterPathEducate';
import YearSemesterInput from 'components/common/input/YearSemesterInput';
// import { schoolYearsTime } from 'dump/data';
import { BtnSearch, BtnClear } from 'components/common/button';
import './style.scss';

const { Option } = Select;

@Form.create({ name: 'formSerchIndividual' })
class FormInfoCommon extends Component {
  state = {};

  handleReset = () => {
    this.props.form.resetFields();
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, formValues) => {
      const { learnTypes } = formValues;

      if(!isEmpty(learnTypes)) {
        learnTypes.forEach(type => { formValues[type] = true });
      }

      this.props.handleSearch(formValues);
    });
  };

  render() {
    const { getFieldDecorator } = this.props.form;
    const optionFieldDecorator = {
      trainTypeId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Hệ!',
          },
        ],
      },
      facultyId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Khoa!',
          },
        ],
      },
      courseId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Khóa',
          },
        ],
      },
      studyFieldId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn CN',
          },
        ],
      },
      semesterId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn lớp!',
          },
        ],
      },
      yearEduId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn năm học!',
          },
        ],
      },
    };
    return (
      <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <FilterPathEducate form={this.props.form} optionFieldDecorator={optionFieldDecorator} classes={{ isShow: false }} />
          <YearSemesterInput form={this.props.form} span={5} optionFieldDecorator={optionFieldDecorator} />
          <Col span={10}>
            <Form.Item label="Loại SV">
              {getFieldDecorator(`learnTypes`, {})(
                <Select
                  mode="multiple"
                  style={{ width: '100%' }}
                  placeholder="Chọn"
                >
                  <Option value="studentRegister">Học lần 1</Option>
                  <Option value="studentNotRegister">Chưa học</Option>
                  <Option value="studentReLearn">Học lại</Option>
                  <Option value="studentDegreeTwo">Học ngành 2</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={4}>
            <div className="btn-group">
              <BtnSearch htmlType="submit" />
              <BtnClear onClick={this.handleReset} />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default FormInfoCommon;



