import React, { Component } from 'react';
import { Table } from 'antd';

class TableCreateNewClass extends Component {
  constructor(props) {
    super(props);
    this.state = {};

    this.columns = [
      {
        title: 'Mã học phần',
        dataIndex: 'subjectId',
        key: 'subjectId',
      },
      {
        title: 'Tên học phần',
        dataIndex: 'subjectLabel',
        key: 'subjectLabel',
      },
      {
        title: 'Số TC',
        dataIndex: 'noOfCredit',
        key: 'noOfCredit',
      },
      {
        title: 'Số tiết',
        dataIndex: 'noOfClassCredit2',
        key: 'noOfClassCredit2',
        render: (text, record) => <div>{record.noOfClassCredit}</div>,
      },
      {
        title: 'Hệ số',
        dataIndex: 'multiplyBy',
        key: 'multiplyBy',
      },
      {
        title: 'Số SV',
        dataIndex: 'soSV',
        key: 'soSV',
      },
      {
        title: 'Ký hiệu tên lớp',
        dataIndex: 'classCode',
        // render: () =>
        //   setTableCreateNewClass.length >= 1 ? (
        //     <Fragment>
        //       <Input />
        //     </Fragment>
        //   ) : null,
      },
      {
        title: 'S.tiết tuần',
        dataIndex: 'periodInWeek',
        // render: () =>
        //   setTableCreateNewClass.length >= 1 ? (
        //     <Fragment>
        //       <Input />
        //     </Fragment>
        //   ) : null,
      },
      {
        title: 'Số lớp TC',
        dataIndex: 'noOfClassCredit',
        key: 'noOfClassCredit',
      },
      {
        title: 'Số SV tối thiểu',
        dataIndex: 'minStudent',
        // render: () =>
        //   setTableCreateNewClass.length >= 1 ? (
        //     <Fragment>
        //       <Input />
        //     </Fragment>
        //   ) : null,
      },
      {
        title: 'Số SV tối đa',
        dataIndex: 'maxStudent',
        // render: () =>
        //   setTableCreateNewClass.length >= 1 ? (
        //     <Fragment>
        //       <Input />
        //     </Fragment>
        //   ) : null,
      },
      {
        title: 'Số ca LT/tuần',
        dataIndex: 'periodInWeek1',
        key: 'periodInWeek1',
        render: (text, record) => <div>{record.periodInWeek}</div>,
      },
      {
        title: 'Số lớp BT, TH, TN',
        dataIndex: 'soLopBTTHTN',
        key: 'soLopBTTHTN',
      },
      {
        title: 'Số ca TH, BT, TN/tuần',
        dataIndex: 'soCaTHBTTNTuan',
        key: 'soCaTHBTTNTuan',
      },
      {
        title: 'Tự chọn',
        dataIndex: 'isSelfChoose',
        // render: () =>
        //   setTableCreateNewClass.length >= 1 ? (
        //     <Fragment>
        //       <Checkbox />
        //     </Fragment>
        //   ) : null,
      },
      {
        title: 'S.tiết LT',
        dataIndex: 'theoryPeriod',
        key: 'theoryPeriod',
      },
      {
        title: 'S.tiết TH',
        dataIndex: 'practicePeriod',
        key: 'practicePeriod',
      },
    ];

    this.rowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
        this.props.handleSelectRow(selectedRowKeys);
      },
      getCheckboxProps: record => ({
        disabled: record.name === 'Disabled User', // Column configuration not to be checked
        name: record.name,
      }),
    };
  }

  render() {
    const { data, loading } = this.props;
    return (
      <div>
        <Table
          bordered
          rowSelection={this.rowSelection}
          dataSource={data}
          columns={this.columns}
          scroll={{ x: 'true' }}
          loading={loading}
        />
      </div>
    );
  }
}

export default TableCreateNewClass;



