import React, { Component } from 'react';
import { Row, Col, Form, InputNumber, Input } from 'antd';
import { DatePickerString, SelectAjax } from 'components/common';
import moment from 'moment';
import FilterPathEducate from 'components/common/input/FilterPathEducate';
import YearSemesterInput from 'components/common/input/YearSemesterInput';
import { BtnSearch, BtnClear } from 'components/common/button';
import { getResgisterSessionSelect } from 'services/registerSession';
import './style.scss';

// @Form.create({ name: 'formSerchIndividual' })
class FormInfoCommonCreateNewClass extends Component {
  state = { registerSession: [], startDate: '' };

  componentWillReceiveProps({ reload }) {
    if (this.props.reload !== reload) {
      this.handleSearch();
    }
  }

  getResgisterSessionSelectCustom = async params => {
    const res = await getResgisterSessionSelect({ ...params, fullFormat: true });
    this.setState({ registerSession: res.data.items });
    return res;
  };

  handleReset = () => {
    this.props.form.resetFields();
  };
  handleSearch = e => {
    if (e) {
      e.preventDefault();
    }
    this.props.form.validateFields((err, values) => {
      this.props.handleSearchSubject(values);
    });
  };

  changeRegisterSessionId = value => {
    const { registerSession } = this.state;
    const dataChoose = registerSession.filter(item => item.value === value);

    const { startDate } = dataChoose[0];
    this.setState({
      startDate,
    });
  };

  changeStartWeek = e => {
    const { startDate } = this.state;

    const { setFieldsValue } = this.props.form;

    let numberStartDate = 0;
    if (e > 1) {
      numberStartDate = (e - 1) * 7;
    } else {
      numberStartDate = 0;
    }

    const calculatorStartDate = moment(startDate)
      .add(numberStartDate, 'days')
      .toDate();
    const formatDate = moment(calculatorStartDate).format('YYYY-MM-DD');
    const formatMondayToWeek = moment(new Date(formatDate))
      .startOf('iweek')
      .isoWeekday(1);
    setFieldsValue({
      fromDate: formatMondayToWeek,
    });
  };

  changeEndWeek = e => {
    const { startDate } = this.state;

    const { setFieldsValue } = this.props.form;

    let numberDate = 0;
    if (e > 1) {
      numberDate = (e - 1) * 7;
    } else {
      numberDate = 0;
    }

    const calculatorEndDate = moment(startDate)
      .add(numberDate, 'days')
      .toDate();
    const formatDate = moment(calculatorEndDate).format('YYYY-MM-DD');
    const formatSunDayToWeek = moment(new Date(formatDate)).day('Sunday');
    // .endOf('iweek')
    // .isoWeekday(1);
    setFieldsValue({
      toDate: formatSunDayToWeek,
    });
  };

  render() {
    const { getFieldDecorator, getFieldValue } = this.props.form;
    const optionFieldDecorator = {
      trainTypeId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Hệ!',
          },
        ],
      },
      facultyId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Khoa!',
          },
        ],
      },
      courseId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Khóa',
          },
        ],
      },
      studyFieldId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn CN',
          },
        ],
      },
      semesterId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn lớp!',
          },
        ],
      },
      yearEduId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn năm học!',
          },
        ],
      },
    };
    // const { Option } = Select;
    const trainTypeId = getFieldValue('trainTypeId');
    const yearEduId = getFieldValue('yearEduId');
    const semesterId = getFieldValue('semesterId');
    return (
      <Form className="form-inline-label" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <FilterPathEducate
            form={this.props.form}
            classes={{ isShow: false }}
            optionFieldDecorator={optionFieldDecorator}
          />
          <YearSemesterInput
            form={this.props.form}
            span={5}
            optionFieldDecorator={optionFieldDecorator}
          />
          <Col span={6}>
            <div className="btn-group">
              <BtnSearch htmlType="submit" />
              <BtnClear onClick={this.handleReset} />
            </div>
          </Col>
        </Row>
        <Row gutter={24}>
          <Col span={5}>
            <Form.Item label="Đợt đăng ký">
              {getFieldDecorator(
                'registerSessionId',
                {}
              )(
                <SelectAjax
                  service={this.getResgisterSessionSelectCustom}
                  params={{ trainTypeId, yearEduId, semesterId, fullformat: true }}
                  allowGetData={!!trainTypeId && !!yearEduId && !!semesterId}
                  onChange={this.changeRegisterSessionId}
                  keyValue="value"
                />
              )}
            </Form.Item>
          </Col>
          <Col span={5}>
            <Form.Item label="Từ tuần:">
              {getFieldDecorator(
                `fromWeek`,
                {}
              )(<InputNumber onChange={this.changeStartWeek} min={1} max={50} />)}
            </Form.Item>
          </Col>
          <Col span={4}>
            <Form.Item label="Đến tuần:">
              {getFieldDecorator(
                `toWeek`,
                {}
              )(
                <InputNumber
                  onChange={this.changeEndWeek}
                  min={1}
                  max={50}
                  // defaultValue={1}
                  className="toWeek"
                />
              )}
            </Form.Item>
          </Col>
          <Col span={5}>
            <Form.Item label="Từ ngày:">
              {getFieldDecorator(`fromDate`, {})(<DatePickerString className="fromDate" />)}
            </Form.Item>
          </Col>
          <Col span={5}>
            <Form.Item label="Đến ngày:">
              {getFieldDecorator(`toDate`, {})(<DatePickerString />)}
            </Form.Item>
          </Col>
        </Row>
        <Row gutter={24}>
          <Col span={5}>
            <Form.Item label="Ký hiệu lớp TC:">
              {getFieldDecorator(`classCode`, {})(<Input />)}
            </Form.Item>
          </Col>
          <Col span={5}>
            <Form.Item label="Số tiết tuần:">
              {getFieldDecorator(`periodInWeek`, {})(<Input />)}
            </Form.Item>
          </Col>
          <Col span={5}>
            <Form.Item label="Số SV tối thiểu:">
              {getFieldDecorator(`minStudentNo`, {})(<Input />)}
            </Form.Item>
          </Col>
          <Col span={5}>
            <Form.Item label="Số SV tối đa:">
              {getFieldDecorator(`maxStudentNo`, {})(<Input />)}
            </Form.Item>
          </Col>
        </Row>
        <br />
      </Form>
    );
  }
}

export default FormInfoCommonCreateNewClass;



