import { request } from 'utils';

const prefix = '/api/education';

export const getResgisterSessionSelect = ({
  trainTypeId,
  yearEduId,
  semesterId,
  fullFormat,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
} = {}) =>
  request({
    prefix,
    url: '/RegisterSession/DataForSelect',
    method: 'GET',
    params: {
      trainTypeId,
      yearEduId,
      semesterId,
      fullFormat,
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
    data: {},
  });

export const getRegisterSession = ({
  trainTypeId = 1,
  yearEduId,
  semesterId,
  page,
  pageSize,
} = {}) =>
  request({
    prefix,
    url: '/RegisterSession',
    method: 'GET',
    params: { trainTypeId, yearEduId, semesterId, page, pageSize },
    data: {},
  });

export const getRegisterSessionById = id =>
  request({
    prefix,
    url: `/RegisterSession/${id}`,
    method: 'GET',
    params: {},
    data: {},
  });

export const createRegisterSession = async values => {
  await request({
    prefix,
    url: `/RegisterSession`,
    method: 'POST',
    params: {},
    data: { ...values },
  });
};

export const updateRegisterSession = async values => {
  const { id } = values;
  await request({
    prefix,
    url: `/RegisterSession/${id}`,
    method: 'PUT',
    params: {},
    data: { ...values },
  });
};

export const deleteRegisterSession = id =>
  request({
    prefix,
    url: `/RegisterSession/${id}`,
    method: 'DELETE',
    params: {},
    data: {},
  });



